.mask {
  box-sizing: border-box;
  border: 1px solid #bdc1c5;
  border-radius: 5px;
  background-color: #ffffff;
}

/* TODO(mdc-migration): The following rule targets internal classes of slider that may no longer apply for the MDC version. */
::ng-deep .mat-slider-thumb-label {
  height: 20px !important;
  width: 20px !important;
  text-align: center;
}

/* TODO(mdc-migration): The following rule targets internal classes of slider that may no longer apply for the MDC version. */
::ng-deep .mat-slider-horizontal .mat-slider-thumb-label {
  transform: rotate(45deg) !important;
}

/* TODO(mdc-migration): The following rule targets internal classes of slider that may no longer apply for the MDC version. */
::ng-deep .mat-slider-horizontal .mat-slider-thumb-label-text {
  transform: rotate(-45deg) !important;
}

::ng-deep ngx-slider-tooltip-wrapper .ngx-slider-inner-tooltip {
  background-color: white;
}

::ng-deep ngx-slider .ngx-slider-pointer {
  cursor: pointer;
  width: 15px !important;
  height: 15px !important;
  top: -5px !important;
  background-color: #0f72ad !important;
  z-index: 3;
  border-radius: 16px;
}

::ng-deep .ngx-slider .ngx-slider-pointer.ngx-slider-active:after {
  content: none !important;
}
::ng-deep .ngx-slider-bubble {
  font-size: 12px !important;
  bottom: 5px !important;
}

::ng-deep .ngx-slider-ticks {
  margin-left: -2% !important;
}
::ng-deep .ngx-slider-tick {
  width: 5px !important;
}
